import { MethodHttp, MyHttp } from "../helper/myaxios";

export class HomeService {
    static GetListLogVersion() {
        return MyHttp.SendReqV2({
            method: MethodHttp.Get,
            url: `v2/home/aboutVersion`,
        });
    }

    static GetListMenu(isMobi: boolean) {
        return MyHttp.SendReqV2({
            method: MethodHttp.Get,
            url: `v2/home/renderMenus?isMobi=${isMobi}`,
            isNoAuth: true
        });
    }
}