import { useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { MyHttp } from "../../helper/myaxios";
function MyRouter() {
  let location = useLocation();
  let navigate = useNavigate();

  useEffect(() => {
    if (MyHttp.AccessToken !== null && (location.pathname === "/login" || location.pathname === "/forgotPassword")) {
      navigate("/");
    }

    if (MyHttp.AccessToken === null && (location.pathname !== "/login" && location.pathname !== "/forgotPassword" && !location.pathname.startsWith("/docApis"))) {
      navigate("/login");
      MyHttp.SetAccessToken(null);
    }
  }, [location, navigate]);

  return (
    <div>
    </div>
  );
}

export default MyRouter
