import { Col, Row } from "antd";
import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { Box } from "../core/MyBox";
import { MyButton } from "../core/MyButton";
import { MenuOutlined, LeftOutlined } from "@ant-design/icons";
import Search from "antd/lib/input/Search";

export default function LanguageComponent(props: any) {
    const [lang, setLang] = useState(localStorage.getItem('lang')?.toString());

    function handleChangeLang(val: any) {
        setLang(val.target.value);
        localStorage.setItem('lang', val.target.value);
        window.location.reload();
    }

    useEffect(() => {
        if (lang === undefined) {
            setLang('vi');
            localStorage.setItem('lang', 'vi');
        }
    }, [lang]);

    function handleSearch(val: string) {
        // command auto        
    }

    const navigate = useNavigate();
    return (
        <Box className="box-navbar">
            <Row>
                <Col span={2} xl={2} md={3} xs={3}>
                    <MyButton
                        onClick={() => { navigate(-1) }}
                        value={<div><LeftOutlined /> Trở về</div>} />
                </Col>
                <Col span={2} xl={2} md={3} xs={3}>
                    <MyButton
                        onClick={props.handleClickMenu}
                        value={<MenuOutlined />} />
                </Col>
                <Col span={3} xl={3} md={2} xs={2}>
                    <Link to="/">
                        <h3>Trang chủ</h3>
                    </Link>
                </Col>
                <Col span={6} xl={6} md={0} xs={0}>
                    <Search
                        onSearch={handleSearch} />
                </Col>
                <Col span={3}>

                </Col>
                <Col span={6} style={{textAlign: "right"}}>
                    <select value={lang} onChange={handleChangeLang}>
                        <option value="vi">Tiếng Việt</option>
                        <option value="en">Tiếng Anh</option>
                    </select>
                </Col>
            </Row>
        </Box>
    )
}