import { message } from "antd";

export enum MethodHttp {
    Get,
    Post,
    Put,
    Delete
}

export class MyHttp {
    static Lang: string | null = localStorage.getItem('lang');
    static BaseURL: string | undefined = "https://myweb-api.huynq.xyz/api";
    // static BaseURL: string | undefined = "https://myweb-api-dev.huynq.xyz/api";

    // ============================= AccessToken =================================
    static AccessToken: string | null = localStorage.getItem('accessToken');
    static SetAccessToken(token: string | null) {
        this.AccessToken = token;
        token === null ? localStorage.removeItem("accessToken") : localStorage.setItem("accessToken", token)
    }

    static GetAccessToken() { return this.AccessToken }

    // ============================= RefreshToken =================================
    static RefreshToken: string | null = localStorage.getItem('refreshToken');
    static SetRefreshToken(token: string | null) {
        this.RefreshToken = token;
        if (token === null) {
            localStorage.removeItem("refreshToken");
        } else {
            localStorage.setItem("refreshToken", token);
        }
    };

    static GetHeaderDefault(isNoToken: boolean | false = false) {
        if (isNoToken) {
            return {
                'Content-Type': 'application/json',
                'Language': this.Lang,
                // 'Content-Type': 'application/x-www-form-urlencoded',
            };
        }
        return {
            'Content-Type': 'application/json',
            'Authorization': this.AccessToken !== null ? `Bearer ${this.AccessToken}` : '',
            'Language': this.Lang,
            // 'Content-Type': 'application/x-www-form-urlencoded',
        };
    }

    static ParseMethod(methodStr: string) {
        let method: MethodHttp;
        switch (methodStr) {
            case "Get":
                method = MethodHttp.Get;
                break;
            case "Post":
                method = MethodHttp.Post;
                break;
            case "Put":
                method = MethodHttp.Put;
                break;
            case "Delete":
                method = MethodHttp.Delete;
                break;
            default:
                method = MethodHttp.Get;
                break;
        }
        return method;
    }

    static ParseMethodToString(method: MethodHttp | undefined) {
        let methodStr: string | undefined;
        switch (method) {
            case MethodHttp.Get:
                methodStr = "Get";
                break;
            case MethodHttp.Post:
                methodStr = "Post";
                break;
            case MethodHttp.Put:
                methodStr = "Put";
                break;
            case MethodHttp.Delete:
                methodStr = "Delete";
                break;
            default:
                methodStr = "Get";
                break;
        }
        return methodStr;
    }

    static async SendReq(url: string, method: string, data?: any, isBlob?: boolean) {
        let methodEnum: MethodHttp = this.ParseMethod(method);
        let body = isBlob && methodEnum === MethodHttp.Post ? data : JSON.stringify(data);
        let headers: any = this.GetHeaderDefault();

        var response: Response = {
            isSuccess: true,
            message: "",
            data: {}
        };

        try {
            await fetch(`${this.BaseURL}/${url}`, {
                headers: headers,
                method: this.ParseMethodToString(methodEnum),
                body: body
            })
                .then((res) => {
                    if (isBlob && method !== "Post") return res.blob();
                    if (!res.ok && res.status !== 400) {
                        response = new Response(false, `Server error: ${res.status}`, {});
                    }
                    return res.json()
                })
                .then((data: any) => {
                    if (isBlob && method !== "Post") {
                        response = new Response(true, ``, data);
                        return;
                    }

                    if (data.statusCode >= 500) { return; } // đã catch ở trên
                    response = new Response(data.statusCode === 200, data.message, data.data);
                })
                .catch((err) => response = err);
        }
        catch (err) { console.log(err) };
        return response;
    }

    static async SendReqV2(req: Request) {
        let body = req.isBlob && req.method === MethodHttp.Post ? req.data : JSON.stringify(req.data);
        let headers: any = this.GetHeaderDefault(req.isNoAuth);

        var response: Response = {
            isSuccess: true,
            message: "",
            data: {}
        };

        try {
            await fetch(`${this.BaseURL}/${req.url}`, {
                headers: headers,
                method: this.ParseMethodToString(req.method),
                body: body
            })
                .then((res) => {
                    if (req.isBlob && req.method !== MethodHttp.Post) return res.blob();
                    if (!res.ok && res.status !== 400) {
                        response = new Response(false, `Server error: ${res.status}`, {});
                    }
                    return res.json()
                })
                .then((data: any) => {
                    if (req.isBlob && req.method !== MethodHttp.Post) {
                        response = new Response(true, ``, data);
                        return;
                    }

                    if (data.statusCode >= 500) { return; } // đã catch ở trên
                    response = new Response(data.statusCode === 200, data.message, data.data);
                })
                .catch((err) => response = err);
        }
        catch (err) { console.log(err) };
        return response;
    }
}

class Response {
    isSuccess: boolean | undefined;
    message: string | undefined;
    data: any

    constructor(_isSuccess: boolean, _message: string, _data: any) {
        this.isSuccess = _isSuccess;
        this.message = _message;
        this.data = _data;

        if (!_isSuccess) {
            message.error(_message);
        }
    }
}

class Request {
    url: string | undefined;
    method: MethodHttp | undefined;
    data?: any;
    isBlob?: boolean;
    isNoAuth?: boolean
}

